/*
The main SCSS file for everything, yep
*/

@import "typography";



.header-wrap {
	background: url(https://source.unsplash.com/nqSUnXINsp4/1600x900) no-repeat center center fixed; 
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  height: 47em;
}

.ui.container.main-content {
	margin: 7em 0;
}

.ui.small.icon.buttons {
	margin-bottom: 10px;
}

.ui.borderless.main.menu {
	border: none;
    margin: 0;
}

.workshop-list {
	padding: 0;
}

.workshop-heading {
	margin-top: 0;
}


.workshop-description {
	clear: both;
}


.workshop-post-content {
	width: 25em;
}



ul {
	list-style-type: circle;
}

footer ul {
	list-style: none;
	padding: 0;
}

.workshop-featured-image {
	margin-bottom: 3em;
}

.pay-btn {
	margin-top: 1em !important;
}


.ui.sticky {
	position: relative;
	left: 1.5px !important;
    top: -11px;
    transition: top 0.5s ease-in;
}

.ui.sticky.fixed.top {
	top:80px !important;
	left: 211px !important;
}

nav ul {
	list-style-type: none;
}

nav ul li {
	display: inline;
	margin-right: 1em;
}

.stick {
	position: sticky;
	top: 180px;
}

.footer {
	background-color: 'lavenderblush';
}

.tag-list {
	list-style: none;
	padding: 0;
}

.tag {
	display: inline;
}


.mini-date {
	float: right;
}


.workshop-notes {
  margin-top: 1em;
}